"use client";

import { useState, useEffect } from "react";
import dynamic from "next/dynamic";
import { motion } from "framer-motion";
import { toast } from "react-hot-toast";

const Globe = dynamic(() => import("react-globe.gl"), { ssr: false });

export default function IntelligenceCommandCenter() {
  // === STATE MANAGEMENT ===
  const [intelFeed, setIntelFeed] = useState<any[]>([]);
  const [headline, setHeadline] = useState("");
  const [summary, setSummary] = useState("");
  const [region, setRegion] = useState("");
  const [sentiment, setSentiment] = useState("Neutral");
  const [impact, setImpact] = useState(50);
  const [attn, setAttn] = useState("General");
  const [anonymous, setAnonymous] = useState(false);
  const [glowColor, setGlowColor] = useState("none");
  const [loading, setLoading] = useState(false);

  // === SIMULATED INTEL FEED (for now) ===
  useEffect(() => {
    setIntelFeed([
      {
        country: "Russia",
        title: "Kitty is hungry",
        summary: "My cat is growling",
        impact: 50,
        sentiment: "Negative",
        level: "Low (R)",
      },
      {
        country: "USA",
        title: "Are we there yet.",
        summary: "We saw who did it",
        impact: 50,
        sentiment: "Positive",
        level: "Low (R)",
      },
      {
        country: "China",
        title: "China cuts lending rates to boost growth",
        summary:
          "Beijing announces new rate cuts to stimulate economy.",
        impact: 68,
        sentiment: "Positive",
        level: "Low (R)",
      },
    ]);
  }, []);

  // === HANDLE SUBMIT ===
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    const intel = {
      headline,
      summary,
      region,
      sentiment,
      impact,
      attn,
      anonymous,
    };

    try {
      // === Call AIVE Edge Function ===
      const response = await fetch(
        process.env.NEXT_PUBLIC_AIVE_FUNCTION_URL!,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(intel),
        }
      );

      if (!response.ok) throw new Error("AIVE fetch failed");

      const result = await response.json();
      console.log("AIVE Result:", result);

      // === Glow intensity based on AI confidence ===
      const confidence = result.confidence || 0.5; // 0‚Äì1 range
      if (confidence > 0.8) setGlowColor("gold");
      else if (confidence > 0.5) setGlowColor("cyan");
      else setGlowColor("red");

      toast.success("üß† Intel successfully analyzed by A.I.V.E.!");
      setHeadline("");
      setSummary("");
      setRegion("");
    } catch (err: any) {
      console.error("A.I.V.E. connection error:", err);
      const message =
        err.message?.includes("fetch") || err.name === "TypeError"
          ? "‚ö†Ô∏è Could not connect to A.I.V.E. (Check your Edge Function URL or CORS settings)."
          : "‚ùå Unexpected error ‚Äî see console for details.";
      toast.error(message);
      setGlowColor("red");
    } finally {
      setLoading(false);
    }
  };

  // === VISUAL GLOW PULSE ===
  const glowStyle = {
    boxShadow:
      glowColor === "gold"
        ? "0 0 40px 10px rgba(255,215,0,0.5)"
        : glowColor === "cyan"
        ? "0 0 40px 10px rgba(0,255,255,0.4)"
        : glowColor === "red"
        ? "0 0 40px 10px rgba(255,0,0,0.4)"
        : "none",
    borderRadius: "50%",
    transition: "box-shadow 0.6s ease-in-out",
  };

  return (
    <main className="min-h-screen bg-gradient-to-b from-slate-900 via-slate-950 to-black text-slate-200 flex">
      {/* === LEFT PANEL === */}
      <div className="w-1/2 p-6 space-y-4">
        <h1 className="text-xl font-semibold text-blue-400">
          eX Intelligence Command Center
        </h1>
        <p className="text-sm text-green-400">‚úÖ Live connection established</p>

        {/* Filter buttons */}
        <div className="flex space-x-2">
          {["All", "High", "Medium", "Low", "Restricted"].map((f) => (
            <button
              key={f}
              className="px-2 py-1 text-xs rounded-md bg-slate-800 hover:bg-slate-700"
            >
              {f}
            </button>
          ))}
        </div>

        {/* Intel Feed */}
        <div className="space-y-3 overflow-y-auto max-h-[80vh]">
          {intelFeed.map((intel, i) => (
            <div
              key={i}
              className="p-3 rounded-lg bg-slate-800/60 border border-slate-700"
            >
              <h3 className="font-semibold text-slate-100">{intel.title}</h3>
              <p className="text-sm text-slate-400">{intel.summary}</p>
              <div className="mt-2 flex justify-between text-xs text-slate-500">
                <span>üåç {intel.country}</span>
                <span>
                  Impact: {intel.impact} ‚Ä¢ {intel.sentiment} ‚Ä¢ {intel.level}
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* === RIGHT PANEL (Globe + Form) === */}
      <div className="w-1/2 flex flex-col items-center justify-center space-y-4">
        <motion.div
          animate={{
            scale: [1, 1.05, 1],
            opacity: [0.9, 1, 0.9],
          }}
          transition={{ duration: 3, repeat: Infinity }}
          style={glowStyle}
        >
          <div className="h-64 w-64">
            <Globe
              globeImageUrl="//unpkg.com/three-globe/example/img/earth-dark.jpg"
              backgroundColor="rgba(0,0,0,0)"
              animateIn={true}
              atmosphereColor="deepskyblue"
              atmosphereAltitude={0.25}
              enablePointerInteraction={true}
            />
          </div>
        </motion.div>

        {/* Region Buttons */}
        <div className="flex flex-wrap gap-2 justify-center">
          {[
            "United States",
            "China",
            "India",
            "Russia",
            "Brazil",
            "Germany",
            "France",
            "Mexico",
            "Saudi Arabia",
            "Canada",
            "Australia",
          ].map((c) => (
            <button
              key={c}
              className="px-3 py-1 text-xs bg-slate-800 hover:bg-slate-700 rounded-md"
              onClick={() => setRegion(c)}
            >
              {c}
            </button>
          ))}
        </div>

        {/* Sentiment Counters */}
        <div className="flex space-x-4 text-xs text-slate-400">
          <span>üü¢ Positive: 6</span>
          <span>üü° Neutral: 0</span>
          <span>üî¥ Negative: 2</span>
          <span>‚ö´ Unknown: 0</span>
        </div>

        {/* === INTEL FORM === */}
        <form
          onSubmit={handleSubmit}
          className="bg-slate-900/70 p-4 rounded-xl border border-slate-700 w-80"
        >
          <h3 className="font-semibold text-center mb-3 text-slate-100">
            üß† Add New Intel
          </h3>

          <input
            type="text"
            placeholder="Headline"
            value={headline}
            onChange={(e) => setHeadline(e.target.value)}
            className="w-full mb-2 p-2 rounded bg-slate-800 border border-slate-700 text-sm"
          />
          <textarea
            placeholder="Summary"
            value={summary}
            onChange={(e) => setSummary(e.target.value)}
            className="w-full mb-2 p-2 rounded bg-slate-800 border border-slate-700 text-sm"
          />
          <input
            type="text"
            placeholder="Region (e.g., Canada)"
            value={region}
            onChange={(e) => setRegion(e.target.value)}
            className="w-full mb-2 p-2 rounded bg-slate-800 border border-slate-700 text-sm"
          />

          <div className="flex items-center space-x-2 mb-2">
            <select
              value={sentiment}
              onChange={(e) => setSentiment(e.target.value)}
              className="w-1/2 p-2 rounded bg-slate-800 border border-slate-700 text-sm"
            >
              {["Positive", "Neutral", "Negative"].map((s) => (
                <option key={s}>{s}</option>
              ))}
            </select>
            <input
              type="number"
              value={impact}
              onChange={(e) => setImpact(parseInt(e.target.value))}
              className="w-1/2 p-2 rounded bg-slate-800 border border-slate-700 text-sm"
              min={0}
              max={100}
            />
          </div>

          <div className="flex flex-col space-y-2 text-sm text-slate-400 mb-2">
            <input type="file" className="text-xs" />
            <select
              value={attn}
              onChange={(e) => setAttn(e.target.value)}
              className="p-2 rounded bg-slate-800 border border-slate-700"
            >
              {["General", "FBI", "DEA", "Interpol", "Homeland Security"].map(
                (a) => (
                  <option key={a}>{a}</option>
                )
              )}
            </select>
            <label className="flex items-center space-x-2">
              <input
                type="checkbox"
                checked={anonymous}
                onChange={() => setAnonymous(!anonymous)}
              />
              <span className="text-xs">
                Submit anonymously (no optional identifiers stored)
              </span>
            </label>
          </div>

          <button
            type="submit"
            disabled={loading}
            className={`w-full p-2 rounded font-semibold text-sm transition ${
              loading
                ? "bg-blue-500/60 text-slate-300"
                : "bg-blue-600 hover:bg-blue-500 text-white"
            }`}
          >
            {loading ? "üß† A.I.V.E. Analyzing..." : "üöÄ Submit Intel"}
          </button>
        </form>
      </div>
    </main>
  );
}
